import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css";

const StaticTable = ({
  columnHeaders,
  handleCheckboxChange,
  checkedItems,
  handleSelectChange,
  selectedAttributes,
  mappedAttributes, // Passed mapped attributes
  mandatoryAttributes,
}) => {
  const { allNLSValues = [], dropdownOptions = [] } = mappedAttributes || {};

  console.log("Mapped Attributes prop:", mappedAttributes);
  console.log("columnHeaders", columnHeaders);
  console.log("static table recieved manditory Attribute", mandatoryAttributes);
  console.log("Dropdown Options :", dropdownOptions);

  React.useEffect(() => {
    console.log("Debug NLS matches:");
    console.log("All NLS Values:", allNLSValues);
    columnHeaders.forEach((header) => {
      console.log(`Checking header: "${header}"`);
      console.log(
        `Available NLS matches:`,
        allNLSValues.filter(
          (nls) => nls.toLowerCase().trim() === header.toLowerCase().trim()
        )
      );
    });
  }, [allNLSValues, columnHeaders]);

  const headerMappings = {
    "release phase": "phase",
    "long description": "description",
    // Add more mappings as needed
  };

  const hasMatchingNLS = (header) => {
    if (!Array.isArray(allNLSValues)) {
      console.warn("allNLSValues is not an array:", allNLSValues);
      return false;
    }

    const normalizedHeader = header.toLowerCase().trim();

    // First try direct matching
    const directMatch = allNLSValues.some(
      (nls) => nls.toLowerCase().trim() === normalizedHeader
    );

    if (directMatch) return true;

    // Try matching using the mapping dictionary
    const mappedValue = headerMappings[normalizedHeader];
    if (mappedValue) {
      const mappedMatch = allNLSValues.some((nls) =>
        nls.toLowerCase().trim().includes(mappedValue)
      );
      if (mappedMatch) {
        console.log(
          `Header "${header}" matched via mapping to "${mappedValue}"`
        );
        return true;
      }
    }

    // Try partial matching as a last resort
    const partialMatch = allNLSValues.some((nls) => {
      const normalizedNLS = nls.toLowerCase().trim();
      return (
        normalizedHeader.includes(normalizedNLS) ||
        normalizedNLS.includes(normalizedHeader)
      );
    });

    console.log(`Header "${header}" match result:`, partialMatch ? "✅" : "❌");
    return partialMatch;
  };

  return (
    <Table bordered className="table-light ">
      <thead>
        <tr>
          <th className="header-styling"> Column Name</th>
          <th className="header-styling">Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((header, index) => {
          const isMandatory = mandatoryAttributes.includes(header);
          const hasNLS = hasMatchingNLS(header);
          // Log the state of each row
          console.log(`Row ${index}:`, {
            header,
            hasNLS,
            isMandatory,
            defaultLabel: hasNLS ? header : "",
          });
          return (
            <tr key={index}>
              <td>{header}</td>
              <td>
                <CustomSelect
                  index={index}
                  selectedValue={selectedAttributes[index] || header}
                  onChange={handleSelectChange}
                  size="lg"
                  className="w-100"
                  disabled={!hasNLS}
                  options={{
                    defaultLabel: hasNLS
                      ? header
                      : "Please select from Drop Down", // Show header only if NLS matches
                    list: dropdownOptions.map((attr) => ({
                      // Always show all mapped attributes
                      value: attr.backendName,
                      label: attr.uiLabel,
                    })),
                  }}
                />
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
